<template>
    <div class="modal" v-show="isModal" @click="closeModal">123</div>
    <div class="top"
         @dragover.prevent
         @drop="dragDrop($event)"
    >
        <!--        <cropper-->
        <!--                class="cropper"-->
        <!--                :src="event.image"-->
        <!--                :stencil-props="{-->
        <!--                        handlers: {},-->
        <!--                        movable: false,-->
        <!--                        resizable: false,-->
        <!--                        aspectRatio: 570/285,-->
        <!--                      }"-->
        <!--                :resize-image="{-->
        <!--                        adjustStencil: false-->
        <!--                      }"-->
        <!--                image-restriction="stencil"-->
        <!--                :default-size="defaultSize"-->
        <!--                @change="change"-->
        <!--        ></cropper>-->
        <img
                :src="event.image"
                alt=""
                @click="modal"
        >
        <div v-if="isText">
            <p contenteditable="true">
                {{ event.description }}
            </p>
            <p
                    class="pushkin"
                    v-if="event.is_pushkin_card"
            >
                Действует Пушкинская карта
            </p>
            <p>
                {{ event.date }} {{ event.time }} {{ event.venue }}
            </p>
            <a :href="event.link" target="_blank">Выбрать место</a>
        </div>
    </div>
</template>

<script>
import plugImage from '@/assets/img/plug600.png'
import {Cropper} from "vue-advanced-cropper";
import 'vue-advanced-cropper/dist/style.css';

export default {
    components: {Cropper},
    props: {
        template: {
            type: Object
        },
        isText: {
            type: Boolean
        }
    },
    data() {
        return {
            event: {
                description: 'Описание мероприятия',
                date: 'Дата',
                time: 'Время',
                venue: 'Место',
                is_pushkin_card: false,
                image: plugImage
            },
            canvas: null,
            isModal: false
        }
    },
    methods: {
        dragDrop(event) {
            const data = JSON.parse(event.dataTransfer.getData('event'))
            this.event = Object.assign(this.event, data)
        },
        change({coordinates, canvas}) {

        },
        defaultSize({imageSize, visibleArea}) {
            return {
                width: (visibleArea || imageSize).width,
                height: (visibleArea || imageSize).height,
            };
        },
        modal() {
            this.isModal = true
        },
        closeModal() {
            this.isModal = false
        }
    }
}

</script>

<style lang="scss" scoped>
.top {
  position: relative;
  text-align: center;
  border-radius: 5px;
  font-family: Tahoma, Geneva, sans-serif;
  font-size: 14px;
  font-weight: bold;
  margin: 0 0 30px;
}

p {
  margin: 20px 0;
}

img {
  width: 570px;
  height: 285px;
}

a {
  text-decoration: none;
  font-weight: normal;
  margin: auto;
  display: inline-block;
  background: #8e4adf;
  color: #fff;
  border-radius: 50px;
  padding: 13px 35px 14px;
  font-size: 18px;
  line-height: 15px;
  border: none;
}

.pushkin {
  color: #de39a2;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 650px;
  background: rgba(0, 0, 0, .5);
  z-index: 10;
}
</style>
