<template>
    <div
            class="events"
            v-show="events.length > 0"
    >
        <h4 class="events__title">Список мероприятий</h4>
        <div class="events__list">
            <events-item
                    v-for="event of events"
                    :event="event"
                    :key="event.id"
                    @remove="$emit('remove', event)"
                    @replace="$emit('replace')"
            />
        </div>
    </div>
    <div
            class="events"
            v-show="events.length === 0"
    >
        <h4>Список мероприятий пуст</h4>
    </div>

</template>

<script>
import EventsItem from '@/components/EventsItem.vue';

export default {
    components: {
        EventsItem
    },
    emits: ['remove', 'replace'],
    props: {
        events: {
            type: Array,
            required: true
        }
    }
}
</script>

<style lang="scss" scoped>
.events {
  width: calc(100% - 650px);
  margin-right: 650px;
  padding: 20px;

  &__title {
    margin-bottom: 15px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
}
</style>
