<template>
    <div class="events__card"
         draggable="true"
         @dragstart="dragStart($event, event)"
    >
        <h4>{{ event.title }}</h4>
        <div class="card__body">
            <div><strong>Дата: </strong>{{ event.date }}</div>
            <div><strong>Цена: </strong>{{ event.price }}</div>
            <div><strong>Ценз: </strong><span>{{ event.rate }}</span></div>

            <div class="card__buttons">
                <pink-button
                    @click="$emit('replace')"
                >
                    Заменить
                </pink-button>
                <pink-button
                    @click="$emit('remove', event)"
                >
                    Удалить
                </pink-button>
            </div>
        </div>

    </div>
</template>

<script>
import PinkButton from './UI/PinkButton.vue';

export default {
    components: {
        PinkButton
    },
    props: {
        event: {
            type: Object,
            required: true
        }
    },
    methods: {
        dragStart(event, currentEvent) {
            event.dataTransfer.dropEffect = 'copy'
            event.dataTransfer.effectAllowed = 'copy'
            event.dataTransfer.setData('event', JSON.stringify(currentEvent));
        }
    }
}
</script>
<style lang="scss" scoped>
.events__card {
  width: 190px;
  height: 190px;
  border: 1px #ccc solid;
  border-radius: 6px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 5px;
  background-color: #fff;

  &:hover {
    cursor: move;
    background-color: #f7f7f7;
    color: #8e4adf;
  }
}

h4 {
  margin-bottom: 5px;
}

.card__body {
  div:not(:first-child) {
    margin-top: 10px;

    span {
      padding: 2px 5px;
      font-weight: bold;
      font-size: 12px;
      border-radius: 6px;
      color: #fff;
      background-color: #bebebe;
    }
  }
}

.card__buttons {
  display: flex;
  justify-content: space-between;
}
</style>
