<template>
    <div class="main">
        <div class="main__panel">
            <events-upload
                    @loadEvents="getEvents"
                    ref="replaceEvent"
            />
            <choose-template
                    @choose="choose"
            />
            <violett-button
                style="margin: 0"
                @click="getDemoEvents"
            >
                Получить демоданные из базы данных
            </violett-button>
        </div>
        <events-list
                :events="events"
                @remove="removeEvent"
                @replace="replaceEvent"
        />
        <div class="email">
            <div class="email__template">
                <div class="header">
                    <img src="@/assets/img/header.jpg" alt="">
                </div>
                <email-template
                        :template="template"
                />
                <div class="footer">
                    <img src="@/assets/img/footer.jpg" alt="">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import EventsList from '@/components/EventsList.vue'
import EventsUpload from '@/components/EventsUpload.vue';
import ChooseTemplate from '@/components/ChooseTemplate.vue';
import EmailTemplate from "@/components/EmailTemplate.vue";
import axios from "axios";
import ViolettButton from "@/components/UI/ViolettButton.vue";

export default {
    components: {
        EmailTemplate,
        EventsList,
        EventsUpload,
        ChooseTemplate
    },
    data() {
        return {
            events: [],
            template: 'Формат не выбран'
        }
    },
    methods: {
        removeEvent(event) {
            this.events = this.events.filter(element => element.id !== event.id)
        },
        choose(button) {
            this.template = button.data
        },
        getEvents(eventsArray) {
            Promise.all(eventsArray.map(link => {
                axios.get(`https://9cdc-46-229-111-78.eu.ngrok.io/events?link=${link}`)
                    .then(response => this.events.push(response.data))
            }))
        },
        getDemoEvents() {
            axios.get(`https://9cdc-46-229-111-78.eu.ngrok.io/events/demo`)
                .then(response => this.events.push(...response.data))
        },
        replaceEvent() {
            this.$refs.replaceEvent.modalOpen()
        }
    }
}
</script>

<style lang="scss">
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: Helvetica, Geneva, sans-serif;
  font-size: 14px;
}

.main__panel {
  width: calc(100% - 650px);
  margin-right: 650px;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  border-bottom: 1px #ccc solid;
}

h4 {
  margin-bottom: 15px;
}

.email {
  width: 650px;
  height: 100%;
  position: fixed;
  top: 0;
  right: 0;
  border-left: 1px #ccc solid;
  background-color: #f7f7f7;
  overflow-y: scroll;
}

.email__template {
  width: 600px;
  min-height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
}

.header {
  height: 118px;
}

*[contenteditable] {
  border: 1px #868686 dashed;
  border-radius: 5px;
}
</style>
