<template>
    <div class="root">
        <div
            v-if="template === 'Формат не выбран'"
        >
            Формат не выбран
        </div>
        <top-template
            class="top_1"
            v-if="template.tops > 0"
            :template="template"
            :is-text="true"
        ></top-template>
        <div class="head__poster">
            <poster-template
                v-for="poster in template.headPoster"
                :poster="poster"
            ></poster-template>
        </div>
        <top-template
            class="top_2"
            v-if="template.tops > 1"
            :template="template"
            :is-text="isText"
        ></top-template>
        <div class="footer__poster">
            <poster-template
                v-for="poster in template.footerPoster"
                :poster="poster"
            ></poster-template>
        </div>
        <top-template
            class="top_3"
            v-if="template.tops > 2"
            :template="template"
            :is-text="isText"
        >
        </top-template>
    </div>
</template>

<script>
import TopTemplate from "@/components/TopTemplate.vue";
import PosterTemplate from "@/components/PosterTemplate.vue";

export default {
    components: {PosterTemplate, TopTemplate},
    props: {
        template: {
            tops: {
                type: Number,
            },
            headPoster: {
                type: Number,
            },
            footerPoster: {
                type: Number,
            }
        }
    },
    computed: {
        isText() {
            return this.template.tops > 1 && this.template.footerPoster === 0 && this.template.headPoster === 0
        }
    }
}
</script>

<style lang="scss" scoped>
.head__poster,
.footer__poster {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.root {
  width: 100%;
  min-height: 100%;
  background-color: #fff;
  padding: 15px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
</style>
